<template>
    <div class="shop">
        <div class="shop-header">
            <div class="shop-header-items" v-for="(category, index) in gunCategories" :key="category.name">
                <div class="shop-header-item" :class="selectedCategory.name === category.name ? 'selected' : ''">
                    <h1 @click="SelectCategory(index)">{{category.label}}</h1>
                </div>
            </div>
        </div>
        <div class="shop-content" v-if="selectedCategory !== undefined">
            <div class="shop-column left">
                <div class="shop-column-item" v-for="(weapon, index) in selectedCategory.weapons" :key="index" :class="[weapon.isUnlocked ? 'unlocked' : 'locked', selectedWeapon.name === weapon.name ? 'selected' : '']" @click="SelectWeapon(index)">
                    <div class="item-locked" v-if="!weapon.isUnlocked">
                        <i class="fa-solid fa-lock"></i>
                    </div>
                    <div class="item-image">
                        <img :src="'img/' + weapon.img" alt="">
                    </div>
                    <div class="item-info" v-if="weapon.isUnlocked">
                        <div class="item-info-left">
                            <h3>{{ weapon.label }}</h3>
                        </div>
                        <div class="item-info-right">
                            <h3>{{ GetFormatedPrice(weapon.price) }}</h3>
                            <i class="fa-solid fa-money-bill-1-wave"></i>
                        </div>
                    </div>
                </div>
            </div>
            <div class="shop-column right" v-if="selectedWeapon !== undefined">
                <div class="item-header">
                    <h1>{{ selectedWeapon.label }}</h1>
                    <p>{{ selectedWeapon.details }}</p>
                </div>
                <div class="item-details" v-if="selectedWeapon.data !== undefined">
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Damage</h3>
                            <h3>{{ selectedWeapon.data.damage }}</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.damage, maxDamage) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Fire Rate</h3>
                            <h3>{{ selectedWeapon.data.fr }}</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.fr, maxFireRate) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Accuracy</h3>
                            <h3>{{ selectedWeapon.data.acc }}</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.acc, maxAccuracy) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Range</h3>
                            <h3>{{ selectedWeapon.data.range }}</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.range, maxRange) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Reload Speed</h3>
                            <h3>{{ selectedWeapon.data.reload }}s</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.reload, maxReloadSpeed) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                    <div class="item-detail">
                        <div class="item-detail-text">
                            <h3>Clip Size</h3>
                            <h3>{{ selectedWeapon.data.clip }}</h3>
                        </div>
                        <div class="item-detail-progress">
                            <div :style="{ width: GetProcent(selectedWeapon.data.clip, maxClipsize) + '%' }" class="item-detail-progress-bar"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div class="shop-buttons" v-if="selectedWeapon !== undefined">
            <div class="shop-button" v-if="!hasWeaponInCart(selectedWeapon.name)">
                <button @click="AddWeapon()">Køb Til {{ GetFormatedPrice(selectedWeapon.price) }},- DKK</button>
            </div>
            <div class="shop-button">
                <button @click="close()">Forlad Shoppen</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        hasWeaponInCart: {},
        GetFormatedPrice: {},
        GetProcent: {},
        gunCategories: {},
        addWeaponToCart: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            maxDamage: 100,
            maxFireRate: 500,
            maxAccuracy: 100,
            maxRange: 1000,
            maxReloadSpeed: 10,
            maxClipsize: 40,
            selectedCategory: [],
            selectedCategoryIndex: 0,
            selectedWeapon: [],
            selectedWeaponIndex: 0,
        })
    },

    methods: {
        SelectCategory: function(index) {
            this.selectedCategory = this.gunCategories[index];
            this.selectedCategoryIndex = index;

            this.selectedWeapon = this.selectedCategory.weapons[0];
        },

        SelectWeapon: function(index) {
            const weaponItem = this.selectedCategory.weapons[index];
            if (weaponItem == undefined) return;
            if (!weaponItem.isUnlocked) return;

            this.selectedWeapon = this.selectedCategory.weapons[index];
            this.selectedWeaponIndex = index;
        },

        AddWeapon: function() {
            this.addWeaponToCart(this.selectedCategoryIndex, this.selectedWeaponIndex, 1);
        },
    },

    created: function() {
        this.selectedCategory = this.gunCategories[0];
        this.selectedWeapon = this.selectedCategory.weapons[0];
    },
}
</script>

<style>
.shop-header {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    width: 100%;
    height: 60px;
    background-color: rgba(55,55,55,0.75);
}

.shop-header .shop-header-item {
    font-size: 13px;
    text-transform: uppercase;
    color: #606060;
    opacity: 0.75;
}

.shop-header .shop-header-item.selected h1 {
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.shop-header .shop-header-item h1:hover {
    cursor: pointer;
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.shop-content {
    display: flex;
    height: 450px;
    margin: 20px;
}

.shop-column {
    display: flex;
    width: 100%;
    height: 100%;
}

.shop-column.left {
    flex: 1.5;
    flex-wrap: wrap;
    align-content: flex-start;
}

.shop-column.right {
    flex: 1;
    background-color: #1b2429;
    flex-flow: column;
    justify-content: space-between;
}

.shop-column-item {
    width: calc(33.3% - 30px);
    height: 150px;
    margin: 5px;
    background-color: #303030;
    position: relative;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.shop-column-item .item-locked {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    background: rgba(0, 0, 0, 0.60);
}

.shop-column-item .item-locked i {
    font-size: 2em;
    color: #f6f6f6;
}

.shop-column-item.unlocked.selected, .shop-column-item.unlocked:hover {
    border: 3px solid #f6f6f6;
    transition: all 0.5s ease;
}

.shop-column-item.unlocked:hover {
    cursor: pointer;
}

.shop-column-item .item-image {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.shop-column-item img {
    width: 90%;
    height: 80%;
    object-fit: scale-down;
}

.shop-column-item .item-info {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 30px;
    padding: 0px 5px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #020202;
    color: #f6f6f6;
}

.shop-column-item .item-info-left {
    flex: 1;
    display: flex;
    align-items: center;
}

.shop-column-item .item-info-left h3, .shop-column-item .item-info-right h3 {
   overflow: hidden;
   text-overflow: ellipsis;
   display: -webkit-box;
   line-clamp: 1;
   -webkit-line-clamp: 1;
   -webkit-box-orient: vertical;
}

.shop-column-item .item-info-right {
    flex: 1.25;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.shop-column-item .item-info-right h3 {
    margin-right: 3px;
}

.shop-column .item-header {
    text-align: center;
    padding: 10px 20px;
    color: #f6f6f6;
}

.shop-column .item-details {
    width: 400px;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
    margin: 0 auto;
    margin-bottom: 25px;
}

.shop-column .item-detail {
    width: 50%;
    padding: 10px 10px;
}

.shop-column .item-detail-text {
    display: flex;
    justify-content: space-between;
    color: #f6f6f6;
    margin-bottom: 2px;
}

.shop-column .item-detail-progress {
    width: 100%;
    background-color: #484d50;
}

.shop-column .item-detail-progress-bar {
    height: 3px;
    background-color: #f6f6f6;
}

.shop-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 60px;
    padding: 0px 20px;
}

.shop-buttons .shop-button button {
    text-transform: uppercase;
    font-weight: bold;
    color: #fff;
    background: none;
    border: none;
    width: 200px;
    height: 50px;
    border: 1px solid #f6f6f6;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.shop-buttons .shop-button button:hover {
    background-color: #1b2429;
}
</style>