<template>
    <div class="cart">
        <div class="cart-header"></div>
        <div class="cart-content">
            <div class="cart-table" :class="IsObjectEmpty(shopCart) ? 'nodata' : ''">
                <table class="cart-items" v-if="!IsObjectEmpty(shopCart)">
                    <thead>
                        <tr>
                            <th>Våben</th>
                            <th>Pris</th>
                            <th>Antal</th>
                            <th>Max Antal</th>
                            <th>Total Pris</th>
                            <th>Handling</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(cart, index) in shopCart" :key="index">
                            <td>{{ cart.item.label }}</td>
                            <td>{{ GetFormatedPrice(cart.item.price) }},- DKK</td>
                            <td><input type="text" v-model="cart.quantity" @change="refreshCartItem(index, cart.quantity)" /></td>
                            <td>{{ cart.item.maxAmount }}</td>
                            <td>{{ GetFormatedPrice(cart.quantity * cart.item.price) }},- DKK</td>
                            <td><button @click="removeCartItem(index)">Fjern</button></td>
                        </tr>
                    </tbody>
                </table>
                <div class="cart-empty-items" v-if="IsObjectEmpty(shopCart)">
                    <h1>Du har ingen data endnu...</h1>
                </div>
            </div>
            <div class="cart-buttons">
                <div class="cart-button">
                    <button v-if="!IsObjectEmpty(shopCart)" @click="BuyWeapons()">Køb Til {{ GetFormatedPrice(totalWeaponPrice) }},- DKK</button>
                    <button @click="close()">Forlad Shoppen</button>
                </div>
                <div class="cart-button">
                    <h1>Est. leveringstid: {{ totalDeliverTime }}</h1>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    props: {
        deficitMulitplier: {},
        minimumDelivery: {},
        BuyWeaponsInCart: {},
        removeWeaponFromCart: {},
        weaponCart: {},
        GetFormatedPrice: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            shopCart: [],
        })
    },

    methods: {
        ConvertPrice: function(quantity, price) {
            return this.GetFormatedPrice(quantity * price);
        },

        refreshCartItem: function(index, quantity) {
            const weaponItem = this.shopCart[index];
            if (quantity > weaponItem.item.maxAmount) {
                quantity = weaponItem.item.maxAmount;
            }

            this.shopCart[index].quantity = quantity;
        },

        removeCartItem: function(index) {
            const weaponItem = this.shopCart[index];
            this.removeWeaponFromCart(weaponItem.item.name);

            this.shopCart.splice(index, 1);
        },

        GetTotalMinutes: function() {
            var totalMinutes = this.totalWeaponPrice / (1000 * this.deficitMulitplier);
            if (totalMinutes < this.minimumDelivery) {
                totalMinutes = this.minimumDelivery;
            }

            return totalMinutes;
        },

        BuyWeapons: function() {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/buyweapons`, {
                weaponCart: this.shopCart,
                weaponDelivered: false,
                weapontotalMinutes: this.GetTotalMinutes(),
            }).catch(function (error) {
                console.log(error + ' : Error at buyweapons');
            });

            this.BuyWeaponsInCart();
        },

        IsObjectEmpty: function(obj) {
            return Object.keys(obj).length <= 0;
        },
    },

    computed: {
        totalWeaponPrice() {
            let price = 0;
            for (let i = 0; i < this.shopCart.length; i++) {
                const cartItem = this.shopCart[i];
                let weaponPrice = cartItem.quantity * cartItem.item.price;
                price += weaponPrice;
            }

            return price;
        },

        totalDeliverTime() {
            let totalMinutes = this.GetTotalMinutes();
            let seconds = totalMinutes * 60;

            var days = Math.floor(seconds / (3600*24));
            var hours = Math.floor(seconds % (3600*24) / 3600);
            var minutes = Math.floor(seconds % 3600 / 60);

            var dDisplay = days >= 0 ? days + (days == 1 ? " dag - " : " dage - ") : "";
            var hDisplay = hours >= 0 ? hours + (hours == 1 ? " time - " : " timer - ") : "";
            var mDisplay = minutes >= 0 ? minutes + (minutes == 1 ? " minut" : " minutter") : "";
            return dDisplay + hDisplay + mDisplay;
        },
    },

    created: function() {
        this.shopCart = [];
        for (let key in this.weaponCart) {
            const cart = this.weaponCart[key];
            this.shopCart.push(cart);
        }
    },
}
</script>

<style>
.cart {
    display: flex;
    flex-flow: column;
    width: 100%;
    height: calc(100% - 100px);
}

.cart-header {
    width: 100%;
    height: 5px;
    background-color: rgba(55,55,55,0.75);
}

.cart-content {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    width: 100%;
    height: 100%;
    padding: 20px;
}

.cart-table {
    padding: 10px 20px;
    background-color: #1b2429;
    height: calc(100% - 70px);
}

.cart-table.nodata {
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart-table .cart-empty-items h1 {
    font-size: 2.5em;
    text-transform: uppercase;
    color: #f6f6f6;
}

.cart-content .cart-items {
    border-collapse: collapse;
    width: 100%;
}

.cart-content .cart-items td, .cart-items th {
    padding: 8px;
    height: 45px;
    text-align: center;
}

.cart-items th {
    text-transform: uppercase;
}

.cart-items tbody {
    display: block;
    max-height: 440px;
    overflow: auto;
}

.cart-items thead, tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.cart-content .cart-items th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    color: #f6f6f6;
    font-size: 18px;
}

.cart-content .cart-items th, .cart-content .cart-items td {
    border-right: 1px solid #f6f6f6;
    border-bottom: 1px solid #f6f6f6;
    color: #f6f6f6;
}

.cart-content .cart-items th:last-child, .cart-content .cart-items td:last-child {
    border-right: none;
}

.cart-content .cart-items tr:last-child td {
    border-bottom: none;
}

.cart-items tbody::-webkit-scrollbar {
    display: none;
}

.cart-items input {
    width: 75%;
    height: 100%;
    margin: 0;
    color: #f6f6f6;
    text-align: center;
    background: none;
    border: none;
    border-bottom: 1px solid #f6f6f6;
    font-size: 16px;
}

.cart-items td button {
    text-transform: uppercase;
    font-size: 16px;
    border-radius: 3px;
    color: #fff;
    background: none;
    border: none;
}

.cart-items td button:hover {
    font-weight: bold;
}

.cart-items input:focus {
    outline: none;
}

.cart-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    padding: 10px 20px 0px 20px;
    border-top: 1px solid #f6f6f6;
    margin-top: 10px;
}

.cart-buttons .cart-button button {
    text-transform: uppercase;
    font-weight: bold;
    color: #f6f6f6;
    background: none;
    border: none;
    width: 200px;
    height: 50px;
    border: 1px solid #f6f6f6;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-right: 20px;
}

.cart-buttons .cart-button h1 {
    text-transform: uppercase;
    font-weight: bold;
    color: #f6f6f6;
    font-size: 16px;
}

.cart-buttons .cart-button button:hover {
    background-color: #1b2429;
}
</style>