<template>
    <div class="hacking-minigame">
        <div class="minigame">
            <div class="splash">
                <div class="fa hacker">&#xf21b;</div>
                <span class="text">{{ stateText }}</span>
            </div>
            <div v-if="!isHidden" class="hack" :class="isMirrored ? 'mirrored' : ''">
                <div class="find">
                    <div v-for="(code, index) in correctCodeTable" :key="index">{{ originalCodes[code] }}</div>
                </div>
                <div class="timer">{{ this.timerLabel }}</div>
                <div class="codes">
                    <div v-for="(code, index) in codes" :key="index" :class="IsSelected(index) ? 'red' : ''">{{ code }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        miniGameRespond: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            isStarted: false,
            isHidden: true,
            isMirrored: false,
            timer_start: 0,
            timer_finish: 0,
            timer_time: 0,
            timer_game: 0,
            timerStart: 0,
            stateText: "",
            timerLabel: "",
            codes_pos: 0,
            codes: [],
            originalCodes: [],
            currentCode: -1,
            correctCode: -1,
            correctCodeTable: '',
            setTypes: ['numeric','alphabet','alphanumeric','greek','braille','runes'],
        })
    },

    methods: {
        StartMiniGame: function(type, timeout, interval) {
            this.correctCode = 0;
            this.currentCode = 43;

            if (interval == undefined) interval = 5;
            interval *= 1000;

            let setType = type;
            if (setType == undefined) {
                setType = this.generateSetType();
            }

            if (!this.setTypes.includes(setType)) {
                setType = this.generateSetType();
            }

            if (timeout == undefined) timeout = 60;
            this.stateText = "PREPARING INTERFACE...";
            this.isMirrored = this.random(1, 2) === 1;
            
            this.codes = [];
            for (let i=0; i < 80; i++) {
                this.codes.push(this.randomSetChar(setType) + this.randomSetChar(setType));
            }

            this.originalCodes = [...this.codes];
            
            this.correctCode = this.random(0, this.codes.length - 1);
            this.correctCodeTable = this.getGroupFromPos(this.correctCode);
            
            let selector = this;
            selector.timer_start = selector.sleep(3000, function() {
                selector.stateText = "CONNECTING TO THE HOST";

                selector.timer_start = selector.sleep(3000, function() {
                    selector.stateText = "CONNECTED TO HOST";

                    selector.timer_game = setInterval(selector.moveCodes, interval);

                    selector.isStarted = true;
                    selector.isHidden = false;
                    
                    selector.startTimer(timeout);
                    timeout *= 1000;

                    selector.timer_finish = selector.sleep(timeout, function() {
                        selector.validateAction();
                        selector.timerLabel = "0:00";
                    });
                });
            });
        },

        ResetMiniGame: function(){
            this.isStarted = false;
            this.isHidden = true;

            this.codes_pos = 0;
            this.codes = [];

            this.originalCodes = [];
            this.currentCode = -1;
            this.correctCode = -1;

            clearTimeout(this.timer_start);
            clearTimeout(this.timer_finish);
        },

        moveCodes: function() {
            this.codes_pos++;
            this.codes_pos = this.codes_pos % 80;

            for (let i=0; i < this.codes_pos; i++) {
                this.codes.push(this.codes[i]);
            }

            this.codes.splice(0, this.codes_pos);
        },

        validateAction: function() {
            this.stopTimer();
            if (!this.isStarted) return;

            let current_attempt = (this.currentCode + this.codes_pos);
            current_attempt %= 80;

            let hasWon = current_attempt == this.correctCode;
            this.miniGameRespond(hasWon);

            this.ResetMiniGame();
        },

        startTimer: function(timeout) {
            this.timerStart = new Date();
            this.timer_time = setInterval(this.idleTimer, 1, timeout)
        },

        idleTimer: function(timeout) {
            let timerNow = new Date();
            let timerDiff = new Date();
            timerDiff.setTime(timerNow - this.timerStart);

            let ms = timerDiff.getMilliseconds();
            let sec = timerDiff.getSeconds();

            if (ms < 10) {
                ms = "00"+ms;
            } else if (ms < 100) {
                ms = "0" + ms;
            }

            let ms2 = (999-ms);
            if (ms2 > 99) ms2 = Math.floor(ms2/10);
            if (ms2 < 10) ms2 = "0" + ms2;

            this.timerLabel = (timeout-1-sec) + "." + ms2;
        },

        stopTimer: function() {
            clearInterval(this.timer_time);
            clearInterval(this.timer_game);
        },

        IsSelected: function(index) {
            index = parseInt(index);

            let toDraw = this.getGroupFromPos(this.currentCode);
            return toDraw.includes(index);
        },

        getGroupFromPos: function(pos) {
            let count = 4;
            let group = [pos];

            for(let i=1; i<count; i++){
                if( pos+i >= 80 ){
                    group.push( (pos+i) - 80 );
                } else {
                    group.push( pos+i );
                }
            }
            return group;
        },

        generateSetType: function() {
            let typeSize = this.setTypes.length - 1;
            return this.setTypes[this.random(0, typeSize)];
        },

        randomSetChar: function(sets) {
            let str='?';

            switch(sets){
                case 'numeric':
                    str="0123456789";
                    break;
                case 'alphabet':
                    str="ABCDEFGHIJKLMNOPQRSTUVWXYZ";
                    break;
                case 'alphanumeric':
                    str="ABCDEFGHIJKLMNOPQRSTUVWXYZ0123456789";
                    break;
                case 'greek':
                    str="ΑΒΓΔΕΖΗΘΙΚΛΜΝΞΟΠΡΣΤΥΦΧΨΩ";
                    break;
                case 'braille':
                    str="⡀⡁⡂⡃⡄⡅⡆⡇⡈⡉⡊⡋⡌⡍⡎⡏⡐⡑⡒⡓⡔⡕⡖⡗⡘⡙⡚⡛⡜⡝⡞⡟⡠⡡⡢⡣⡤⡥⡦⡧⡨⡩⡪⡫⡬⡭⡮⡯⡰⡱⡲⡳⡴⡵⡶⡷⡸⡹⡺⡻⡼⡽⡾⡿"+
                        "⢀⢁⢂⢃⢄⢅⢆⢇⢈⢉⢊⢋⢌⢍⢎⢏⢐⢑⢒⢓⢔⢕⢖⢗⢘⢙⢚⢛⢜⢝⢞⢟⢠⢡⢢⢣⢤⢥⢦⢧⢨⢩⢪⢫⢬⢭⢮⢯⢰⢱⢲⢳⢴⢵⢶⢷⢸⢹⢺⢻⢼⢽⢾⢿"+
                        "⣀⣁⣂⣃⣄⣅⣆⣇⣈⣉⣊⣋⣌⣍⣎⣏⣐⣑⣒⣓⣔⣕⣖⣗⣘⣙⣚⣛⣜⣝⣞⣟⣠⣡⣢⣣⣤⣥⣦⣧⣨⣩⣪⣫⣬⣭⣮⣯⣰⣱⣲⣳⣴⣵⣶⣷⣸⣹⣺⣻⣼⣽⣾⣿";
                    break;
                case 'runes':
                    //str="ᚠᚡᚢᚣᚤᚥᚦᚧᚨᚩᚪᚫᚬᚭᚮᚯᚰᚱᚲᚳᚴᚵᚶᚷᚸᚹᚺᚻᚼᚽᚾᚿᛀᛁᛂᛃᛄᛅᛆᛇᛈᛉᛊᛋᛌᛍᛎᛏᛐᛑᛒᛓᛔᛕᛖᛗᛘᛙᛚᛛᛜᛝᛞᛟᛠᛡᛢᛣᛤᛥᛦᛧᛨᛩᛪ";
                    str="ᚠᚥᚧᚨᚩᚬᚭᚻᛐᛑᛒᛓᛔᛕᛖᛗᛘᛙᛚᛛᛜᛝᛞᛟᛤ";
                    break;
            }

            return str.charAt(this.random(0,str.length));
        },

        random: function(min, max) {
            return Math.round(Math.random() * (max - min) + min);
        },

        sleep: function(ms, fn) {
            return setTimeout(fn, ms);
        },

        keyPress: function(event) {
            const key = event.key;
            const valid_keys = ['a','w','s','d','ArrowUp','ArrowDown','ArrowRight','ArrowLeft','Enter'];
            
            if(!this.isStarted) return;
            if(!valid_keys.includes(key)) return;

            switch(key){
                case 'w':
                case 'ArrowUp':
                    this.currentCode -= 10;
                    if(this.currentCode < 0) this.currentCode += 80;
                    break;
                case 's':
                case 'ArrowDown':
                    this.currentCode += 10;
                    this.currentCode %= 80;
                    break;
                case 'a':
                case 'ArrowLeft':
                    this.currentCode--;
                    if(this.currentCode < 0) this.currentCode = 79;
                    break;
                case 'd':
                case 'ArrowRight':
                    this.currentCode++;
                    this.currentCode %= 80;
                    break;
                case 'Enter':
                    this.validateAction();
                    return;
            }
        },
    },

    created: function() {
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function() {
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
.hacking-minigame {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hacking-minigame .minigame {
    margin: 0 auto;
    width: 540px;
    min-width: 540px;
    max-width: 540px;
    background-color: #232832;
    padding: 30px 0;
}

.hacking-minigame .splash {
    width: 100%;
    display: flex;
    flex-flow: column;
    align-items: center;
    color: white;
    font-size: 16px;
}

.hacking-minigame .splash .hacker {
    font-size: 65px;
    margin-bottom: 30px;
}

.hacking-minigame .splash .text {
    font-weight: bold;
}

.hacking-minigame .minigame .find {
    text-align: center;
    color: white;
    font-size: 30px;
	user-select: none;
    margin-top: 10px;
}

.hacking-minigame .minigame .find > div{
    display: inline-block;
}

.hacking-minigame .minigame .timer{
    text-align: center;
    color: green;
    font-size: 14px;
    margin-bottom: 15px;
}

.hacking-minigame .minigame .codes {
    display: flex;
    flex-wrap: wrap;
    width: 400px;
    margin: auto;
}

.hacking-minigame .minigame .codes > div{
    flex: 1 0 10%;
    margin: 7px 0;
    color: white;
    text-align: center;
    font-size: 18px;
}

.hacking-minigame .minigame .codes > div.red{
    color: red;
}

.hacking-minigame .minigame .codes > div.green{
    color: green;
}

.hacking-minigame .minigame .mirrored .find > div,
.hacking-minigame .minigame .mirrored .codes > div {
    -moz-transform: scale(-1, -1);
    -o-transform: scale(-1, -1);
    -webkit-transform: scale(-1, -1);
    transform: scale(-1, -1);
}
</style>