<template>
    <div class="lockpick-minigame">
        <canvas id="canvas" width="300" height="300"></canvas>
    </div>
</template>

<script>
export default {
    props: {
        miniGameRespond: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            isStarted: false,
            corrects: 0,
            neededCorrect: 0,
            canvas: null,
            ctx: null,
            ctxWidth: 0,
            ctxHeight: 0,
            degrees: 0,
            new_degrees: 0,
            time: 0,
            color: "#ff0000",
            txtcolor: "#ffffff",
            bgcolor: "#404b58",
            bgcolor2: "#41a491",
            bgcolor3: "#00ff00",
            key_to_press: null,
            g_start: null,
            g_end: null,
            animation_loop: null,
            timeMultiplier: null,
            hasCanvas: false,
        })
    },

    methods: {
        StartMiniGame: function(corrects, timeMultiplier) {
            if (corrects == undefined) corrects = 5;
            this.neededCorrect = corrects;
            this.corrects = 0;

            if (timeMultiplier == undefined) timeMultiplier = 6;
            this.timeMultiplier = timeMultiplier;

            this.InitDocument();
        },

        InitDocument: function() {
            this.canvas = document.getElementById("canvas");
            this.ctx = this.canvas.getContext("2d");

            this.ctxWidth = this.canvas.width;
            this.ctxHeight = this.canvas.height;

            this.DrawCanvas();
        },

        InitCanvas: function(correct) {
            // Clear the canvas every time a chart is drawn
            this.ctx.clearRect(0,0, this.ctxWidth, this.ctxHeight);

            // Background 360 degree arc
            this.ctx.beginPath();
            this.ctx.strokeStyle = this.bgcolor;
            this.ctx.lineWidth = 20;
            this.ctx.arc(this.ctxWidth / 2, this.ctxHeight / 2, 100, 0, Math.PI * 2, false);
            this.ctx.stroke();

            // Green zone
            this.ctx.beginPath();
            this.ctx.strokeStyle = correct === true? this.bgcolor3 : this.bgcolor2;
            this.ctx.lineWidth = 20;
            this.ctx.arc(this.ctxWidth / 2, this.ctxHeight / 2, 100, this.g_start - 90 * Math.PI / 180, this.g_end - 90 * Math.PI / 180, false);
            this.ctx.stroke();

            // Angle in radians = angle in degrees * PI / 180
            let radians = this.degrees * Math.PI / 180;
            this.ctx.beginPath();
            this.ctx.strokeStyle = this.color;
            this.ctx.lineWidth = 40;
            this.ctx.arc(this.ctxWidth / 2, this.ctxHeight / 2, 90, radians - 0.1 - 90 * Math.PI / 180, radians - 90 * Math.PI / 180, false);
            this.ctx.stroke();

            // Adding the key_to_press
            this.ctx.fillStyle = this.txtcolor;
            this.ctx.font = "100px sans-serif";
            let text_width = this.ctx.measureText(this.key_to_press).width;
            this.ctx.fillText(this.key_to_press, this.ctxWidth / 2 - text_width / 2, this.ctxHeight / 2 + 35);
        },

        DrawCanvas: function() {
            if (typeof animation_loop !== undefined) clearInterval(this.animation_loop);

            this.hasCanvas = true;

            this.g_start = this.getRandomInt(20,40) / 10;
            this.g_end = this.getRandomInt(3,5) / 10;
            this.g_end = this.g_start + this.g_end;

            this.degrees = 0;
            this.new_degrees = 360;

            this.key_to_press = '' + this.getRandomInt(1,4);
            this.time = this.getRandomInt(1, 3) * this.timeMultiplier;

            this.animation_loop = setInterval(this.animate_to, this.time);
        },

        animate_to: function() {
            if (this.degrees >= this.new_degrees) {
                this.CheckMiniGameInput();
                return;
            }

            this.degrees+=2;
            this.InitCanvas();
        },

        CheckMiniGameInput: function() {
            this.bgcolor3 = "#ff0000";

            let d_start = (180 / Math.PI) * this.g_start;
            let d_end = (180 / Math.PI) * this.g_end;

            if (this.hasCanvas == false) return

            let hasWon = !(this.degrees < d_start || this.degrees > d_end)
            if (hasWon == true) {
                this.bgcolor3 = "#00ff00";
                this.corrects++
            }

            this.InitCanvas(true);
            this.hasCanvas = false;
            
            clearInterval(this.animation_loop);

            if (hasWon == false) {
                let selector = this;
                setTimeout(function(){
                    selector.miniGameRespond(false);
                    return;
                }, 2000);
            }

            if (this.neededCorrect <= this.corrects) {
                this.miniGameRespond(true);
                return;
            }

            if (hasWon == true && this.neededCorrect > this.corrects) {
                let selector = this;
                setTimeout(function(){
                    selector.DrawCanvas();
                }, 1000);
            }
        },

        keyPress: function(event) {
            const key = event.key;
            const valid_keys = ['1','2','3','4'];

            if(!valid_keys.includes(key)) return;
            if(key !== this.key_to_press) {
                return;
            }

            this.CheckMiniGameInput();
        },

        getRandomInt: function(min, max) {
            min = Math.ceil(min);
            max = Math.floor(max);
            return Math.floor(Math.random() * (max - min + 1) + min); //The maximum is inclusive and the minimum is inclusive
        }
    },

    created: function() { 
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function() {
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
.lockpick-minigame {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#canvas {
    display: block;
    margin: 50px auto;
    width: 200px;
}
</style>