<template>
    <div id="App" v-show="opened || hasMiniGame || hasMinigameGif">
        <Hacking v-show="hasMiniGame && minigameType === 'hacking'" ref="hacking" :miniGameRespond="miniGameRespond" :NameResource="NameResource" :close="close"/>
        <Thermite v-show="hasMiniGame && minigameType === 'thermite'" ref="thermite" :miniGameRespond="miniGameRespond" :NameResource="NameResource" :close="close"/>
        <Lockpick v-show="hasMiniGame && minigameType === 'lockpick'" ref="lockpick" :miniGameRespond="miniGameRespond" :NameResource="NameResource" :close="close"/>

        <div v-if="hasMinigameGif" class="illegal-gif">
            <img :src="'voltlab/' + minigameGif" alt="">
        </div>

        <div class="illegal-container" v-if="opened">
            <div class="illegal-wrapper">
                <div class="shop-gang">
                    <div class="shop-gang-left">
                        <div class="shop-gang-level">
                            <h1>{{reputation.level}}</h1>
                        </div>
                        <div class="shop-gang-rep">
                            <h1>Reputation <span>{{reputation.value}}/{{reputation.needed}}</span></h1>
                            <div class="shop-gang-rep-progress">
                                <div :style="{ width: GetProcent(reputation.min, reputation.needed) + '%' }" class="shop-gang-rep-progress-bar"></div>
                            </div>
                        </div>
                    </div>
                    <div class="shop-gang-right">
                        <div class="shop-gang-cart" v-if="site != 'weaponcart'">
                            <h1 @click="selectSite('weaponcart')">Din Kurv <i class="fa-solid fa-basket-shopping"></i></h1>
                        </div>
                        <div class="shop-gang-cart" v-if="site != 'weaponshop'">
                            <h1 @click="selectSite('weaponshop')">Våben <i class="fa-solid fa-shield"></i></h1>
                        </div>
                        <div class="shop-gang-profile">
                            <h1 @click="selectSite('weaponprofile')">Din Profil <i class="fa-solid fa-user"></i></h1>
                        </div>
                    </div>
                </div>

                <WeaponShop v-if="site === 'weaponshop'" :hasWeaponInCart="hasWeaponInCart" :gunCategories="gunCategories" :GetProcent="GetProcent" :GetFormatedPrice="GetFormatedPrice" :addWeaponToCart="addWeaponToCart" :NameResource="NameResource" :close="close"/>
                <WeaponCart v-if="site === 'weaponcart'" :minimumDelivery="minimumDelivery" :deficitMulitplier="deficitMulitplier" :BuyWeaponsInCart="BuyWeaponsInCart" :removeWeaponFromCart="removeWeaponFromCart" :weaponCart="weaponCart" :GetFormatedPrice="GetFormatedPrice" :NameResource="NameResource" :close="close"/>
                <WeaponProfile v-if="site === 'weaponprofile'" :CanLevelUp="CanLevelUp" :nextReputation="nextReputation" :GetProcent="GetProcent" :reputationMissions="reputationMissions" :orderedItems="orderedItems" :GetFormatedPrice="GetFormatedPrice" :NameResource="NameResource" :close="close"/>
            </div>
        </div>
    </div>
</template>

<script>

import axios from 'axios';

import Hacking from './minigames/Hacking';
import Thermite from './minigames/Thermite';
import Lockpick from './minigames/Lockpick';

import WeaponShop from './screens/WeaponShop';
import WeaponCart from './screens/WeaponCart';
import WeaponProfile from './screens/WeaponProfile';

export default {
    components: {
        Hacking,
        Thermite,
        Lockpick,
        WeaponShop,
        WeaponCart,
        WeaponProfile,
    },

    data: function(){
        return({
            hasMinigameGif: false,
            minigameGif: '',
            hasMiniGame: false,
            minigameType: '',
            opened: false,
            site: 'weaponshop',
            deficitMulitplier: 20,
            minimumDelivery: 720,
            reputation: {level: 1, value: 50, needed: 250},
            nextReputation: {level: 2, needed: 500, unlocks: ["Pistol .50", "Ceramic Pistol", "Tec-9"]},
            reputationMissions: [
                {img: "rffr.jpg", name: "Valgfri pakket stof", reward: 25, itemRequired: 100, itemDelivered: 100},
                {img: "rffr.jpg", name: "aflevere stoffer", reward: 15, itemRequired: 100, itemDelivered: 50},
                {img: "rffr.jpg", name: "aflevere stoffer", reward: 30, itemRequired: 100, itemDelivered: 10},
                {img: "rffr.jpg", name: "aflevere stoffer", reward: 55, itemRequired: 500, itemDelivered: 0},
            ],
            gunCategories: [
                {name: 'handguns', label: "Pistoler", weapons: [
                    {img: 'pistol.png', maxAmount: 20, isUnlocked: true, label: 'Pistol 9mm', name: 'pistol', price: 500000, details: 'Standard handgun. A .45 caliber combat pistol with a magazine capacity of 12 rounds that can be extended to 16.', data: { damage: 30, fr: 140, acc: 66.66, range: 200, reload: 2, clip: 12 }},
                    {img: 'vintage.png', maxAmount: 20, isUnlocked: true, label: 'Vintage', name: 'vintage', price: 500000, details: 'What you really need is a more recognisable gun. Stand out from the crowd at an armed robbery with this engraved pistol.', data: { damage: 35, fr: 114, acc: 66.66, range: 120, reload: 2, clip: 6 }},
                    {img: 'pistol_50.png', maxAmount: 20, isUnlocked: true, label: 'Pistol .50', name: 'pistol50', price: 700000, details: 'High-impact pistol that delivers immense power but with extremely strong recoil. Holds 9 rounds in magazine.', data: { damage: 60, fr: 103, acc: 40, range: 300, reload: 2, clip: 9 }},
                    {img: 'ceramic.png', maxAmount: 20, isUnlocked: true, label: 'Ceramic Pistol', name: 'ceramic', price: 600000, details: 'Not your grandmas ceramics. Although this pint-sized pistol is small enough to fit into her purse and wont set off a metal detector.', data: { damage: 35, fr: 144, acc: 60, range: 200, reload: 2, clip: 12 }},
                    {img: 'navy.png', maxAmount: 20, isUnlocked: true, label: 'Navy Revolver', name: 'navy', price: 700000, details: 'A true museum piece. You want to know how the West was won - slow reload speeds and a whole heap of bloodshed.', data: { damage: 75, fr: 35, acc: 85, range: 300, reload: 4, clip: 6 }},
                    {img: 'heavy.png', maxAmount: 20, isUnlocked: true, label: 'Heavy Revolver', name: 'heavy', price: 900000, details: 'A handgun with enough stopping power to drop a crazed rhino, and heavy enough to beat it to death if youre out of ammo.', data: { damage: 90, fr: 30, acc: 90, range: 300, reload: 3, clip: 6 }},
                ]},
                {name: 'machine', label: "MGs & SMGs", weapons: [
                    {img: 'mini.png', maxAmount: 20, isUnlocked: true, label: 'Skorpion', name: 'skorp', price: 1800000, details: 'Increasingly popular since the marketing team looked beyond spec ops units and started caring about the little guys in low income areas.', data: { damage: 22, fr: 400, acc: 28, range: 180, reload: 1.5, clip: 20 }},
                    {img: 'tec.png', maxAmount: 20, isUnlocked: true, label: 'Tec-9', name: 'tec', price: 2100000, details: 'This fully automatic is the snare drum to your twin-engine V8 bass: no drive-by sounds quite right without it.', data: { damage: 25, fr: 277, acc: 40, range: 180, reload: 1.5, clip: 12 }},
                    {img: 'micro.png', maxAmount: 20, isUnlocked: true, label: 'Micro SMG', name: 'micro', price: 2900000, details: 'Combines compact design with a high rate of fire at approximately 200-500 rounds per minute.', data: { damage: 30, fr: 308, acc: 50, range: 200, reload: 2, clip: 16 }},
                ]},
                {name: 'assault', label: "Assault rifles", weapons: [
                    {img: 'ak47.png', maxAmount: 20, isUnlocked: true, label: 'AK-47', name: 'ak47', price: 3500000, details: 'This standard assault rifle boasts a large capacity magazine and long distance accuracy.', data: { damage: 50, fr: 280, acc: 28, range: 800, reload: 2, clip: 30 }},
                    {img: 'gusenberg.png', maxAmount: 20, isUnlocked: true, label: 'Gusenberg', name: 'gusenberg', price: 3800000, details: 'Complete your look with a Prohibition gun. Looks great being fired from an Albany Roosevelt or paired with a pinstripe suit.', data: { damage: 30, fr: 303, acc: 25, range: 120, reload: 3, clip: 30 }},
                ]},
                {name: 'shotguns', label: "Shotguns", weapons: [
                    {img: 'pump.png', maxAmount: 20, isUnlocked: true, label: 'Pump Shotgun', name: 'pump', price: 1000000, details: 'Standard shotgun ideal for short-range combat. A high-projectile spread makes up for its lower accuracy at long range.', data: { damage: 20, fr: 63, acc: 40, range: 40, reload: 2, clip: 8 }},
                    {img: 'db_shotgun.png', maxAmount: 20, isUnlocked: true, label: 'Dobbelt Barrel Shotgun', name: 'db', price: 800000, details: 'Standard shotgun ideal for short-range combat. A high-projectile spread makes up for its lower accuracy at long range.', data: { damage: 15, fr: 60, acc: 40, range: 30, reload: 2, clip: 2 }},
                ]},
                {name: 'misc', label: "Misc", weapons: [
                    {img: 'vest.png', maxAmount: 20, isUnlocked: true, label: 'Body Armor', name: 'vest', price: 150000, details: 'Heavy armor platting allowing protection for the torso and vital organs. Favored by mommas boys who dont have time to bleed.'},
                    {img: 'clip.png', maxAmount: 20, isUnlocked: true, label: 'Extended Clip', name: 'clip', price: 350000, details: 'Extended Clips are favored by those who cant hit anything and need a few more shots.'},
                ]},
            ],
            weaponCart: [],
            orderedItems: [],
            NameResource: ["drp_illegaljobs"],
        })
    },

    methods: {
        receiveLua: function(event) {
            if(!event || !event.data) return;
            const lua = event.data;

            if(lua.init) {
                this.NameResource = lua.NameResource;
            }

            if(lua.openWeapon) {
                this.opened = true;
            }

            if(lua.setReputation) {
                this.reputation = lua.reputation;
            }

            if(lua.setNextReputation) {
                this.nextReputation = lua.nextReputation;
            }

            if(lua.setGunCategories) {
                this.gunCategories = lua.gunCategories;
            }

            if(lua.setOrderedItems) {
                this.orderedItems = lua.orderedItems;
            }

            if(lua.setReputationMissions) {
                this.reputationMissions = lua.reputationMissions;
            }

            if(lua.setDeficitMulitplier) {
                this.deficitMulitplier = lua.deficitMulitplier;
            }

            if(lua.setMinimumDelivery) {
                this.minimumDelivery = lua.minimumDelivery;
            }

            if(lua.openMinigameGif) {
                this.hasMinigameGif = true;
                this.minigameGif = lua.minigameGif;
            }

            if(lua.closeMinigameGif) {
                this.hasMinigameGif = false;
                this.minigameGif = '';
            }

            if(lua.openMinigame) {
                if(!lua.minigameType) return;

                this.hasMiniGame = true;
                this.minigameType = lua.minigameType;

                if(this.minigameType === 'hacking') {
                    this.$refs.hacking.StartMiniGame(lua.hackingType, lua.hackingTimeout, lua.hackingInterval);
                }

                if(this.minigameType === 'thermite') {
                    this.$refs.thermite.StartMiniGame(lua.mode, lua.speed);
                }

                if(this.minigameType === 'lockpick') {
                    this.$refs.lockpick.StartMiniGame(lua.corrects, lua.timeMultiplier);
                }
            }
        },

        miniGameRespond: function(respond) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/miniGameRespond`, {
                type: this.minigameType,
                respond: respond,
            }).catch(function (error) {
                console.log(error + ' : Error at miniGameRespond');
            });

            this.close();
        },

        selectSite: function(site) {
            this.site = site;
        },

        addWeaponToCart: function(categoryIndex, weaponIndex) {
            const categoryWeapon = this.gunCategories[categoryIndex];
            if (categoryWeapon == undefined) return;

            const categoryWeaponItem = categoryWeapon.weapons[weaponIndex];
            if (categoryWeaponItem == undefined) return;

            let newItem = { item: categoryWeaponItem, quantity: 1 };
            this.weaponCart[categoryWeaponItem.name] = newItem;

            this.site = "weaponcart";
        },

        removeWeaponFromCart: function(name) {
            delete this.weaponCart[name];
        },

        hasWeaponInCart: function(name) {
            let weaponItem = this.weaponCart[name];
            return weaponItem != undefined && weaponItem != null;
        },

        BuyWeaponsInCart: function() {
            this.weaponCart = [];
            this.site = "weaponprofile";
        },

        CanLevelUp: function() {
            return this.reputation.value >= this.reputation.needed
        },

        GetFormatedPrice: function(oldPrice) {
            let danishLocale = Intl.NumberFormat('da-DK');
            return danishLocale.format(oldPrice)
        },

        GetProcent: function(value, maxValue) {
            return Math.round((value / maxValue) * 100)
        },

        close: function() {
            this.opened = false;

            this.hasMiniGame = false;
            this.minigameType = '';

            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/close`).catch(function (error) {
                console.log(error + ' : Error at close');
            });
        },

        keyPress: function(event) {
            const key = event.key;
            
            if(key == "Escape"){
                this.close();
            }
        },
    },

    created: function(){
        window.addEventListener('message', this.receiveLua);  
        window.addEventListener('keydown', this.keyPress);
    },
    
    destroyed: function(){
        window.removeEventListener('message', this.receiveLua);
        window.removeEventListener('keydown', this.keyPress);
    },
}
</script>

<style>
* {
	padding: 0; 
	margin: 0; 
	box-sizing: border-box; 
    font-family: Roboto;
    overflow: hidden;
}

button:hover {
    cursor: pointer;
}

.illegal-gif {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.illegal-gif img {
    top: 0;
    left: 0;
    width: 100%;
    min-height: 100%;
    position: fixed;
}

.illegal-container {
    display: flex;
    flex-flow: column;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    animation: fadeIn .5s;
    -webkit-animation: fadeIn .5s;
    -moz-animation: fadeIn .5s;
    -o-animation: fadeIn .5s;
    -ms-animation: fadeIn .5s;
}

.illegal-wrapper {
    height: 720px;
    width: 1300px;
    max-width: 90vw;
    color: #0d1134;
    border-radius: 5px;
    background-color: #21292e;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}

.shop-gang {
    display: flex;
    justify-content: space-between;
}

.shop-gang-left {
    padding: 30px;
    display: flex;
}

.shop-gang .shop-gang-level {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40px;
    height: 40px;
    background-color: #1d1d1e;
    transform: rotate(45deg);
    margin-right: 30px;
}

.shop-gang .shop-gang-level h1 {
    transform: rotate(-45deg);
    color: #770b18;
}

.shop-gang .shop-gang-rep {
    color: #686a6d;
    font-size: 12px;
    height: 40px;
    display: flex;
    flex-flow: column;
    justify-content: space-around;
}

.shop-gang .shop-gang-rep-progress {
    width: 100%;
    background-color: #182328;
}

.shop-gang .shop-gang-rep-progress-bar {
    height: 3px;
    background-color: #686a6d;
}

.shop-gang-right {
    padding: 30px;
    display: flex;
    color: #686a6d;
}

.shop-gang-right h1 {
    font-size: 1.5em;
}

.shop-gang-cart, .shop-gang-profile {
    display: flex;
    align-items: center;
}

.shop-gang-cart {
    margin-right: 30px;
}

.shop-gang-cart h1:hover, .shop-gang-profile h1:hover {
    cursor: pointer;
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.shop-gang-cart i, .shop-gang-profile i {
    margin-left: 2.5px;
    font-size: 18px;
}
</style>