<template>
    <div class="profile">
        <div class="profile-header">
            <div class="profile-header-item" :class="selectedPage === 'orders' ? 'selected' : ''">
                <h1 @click="SelectPage('orders')">Mine bestillinger</h1>
            </div>
            <div class="profile-header-item" :class="selectedPage === 'rep' ? 'selected' : ''">
                <h1 @click="SelectPage('rep')">Reputation</h1>
            </div>
        </div>
        <div class="profile-content">
            <div class="profile-orders" :class="IsObjectEmpty(organizedOrders) ? 'nodata' : ''" v-if="selectedPage === 'orders'">
                <table class="profile-items" v-if="!IsObjectEmpty(organizedOrders)">
                    <thead>
                        <tr>
                            <th>Pris</th>
                            <th>Våben</th>
                            <th>Tid til levering</th>
                            <th>Status</th>
                            <th>Handling</th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr v-for="(order, index) in organizedOrders" :key="index">
                            <td>{{ GetFormatedPrice(order.totalPrice) }},- DKK</td>
                            <td><p>{{ order.weapons }}</p></td>
                            <td>{{ GetDeliverTime(order.totalMinutes) }}</td>
                            <td v-if="!order.isDelivered && !order.hasCollected" class="status" :class="order.isDelivered ? 'delivered' : ''">på vej</td>
                            <td v-if="order.isDelivered && !order.hasCollected" class="status" :class="order.isDelivered ? 'delivered' : ''">klar til afhentning</td>
                            <td v-if="order.isDelivered && order.hasCollected" class="status" :class="order.isDelivered ? 'delivered' : ''">afhentet</td>
                            <td v-if="!order.isDelivered && !order.hasCollected"><button @click="cancelOrder(index)">Annullere</button></td>
                            <td v-if="order.isDelivered && !order.hasCollected"><button @click="collectOrder(index)">Afhent</button></td>
                            <td v-if="order.isDelivered && order.hasCollected"><button>Afhentet</button></td>
                        </tr>
                    </tbody>
                </table>
                <div class="profile-empty-items" v-if="IsObjectEmpty(organizedOrders)">
                    <h1>Du har ingen data endnu...</h1>
                </div>
            </div>
            <div class="profile-rep" v-if="selectedPage === 'rep'">
                <div class="profile-column left">
                    <div class="profile-column-item" v-for="(mission, index) in reputationMissions" :key="index">
                        <div class="column-item-content">
                            <div class="item-column">
                                <div class="item-column-subject left">
                                    <h4>{{ mission.name }}</h4>
                                </div>
                                <div class="item-column-subject right">
                                    <div class="item-detail-text">
                                        <h1>{{ mission.itemDelivered }}</h1>
                                        <h1><i class="fa-solid fa-angles-right"></i><i class="fa-solid fa-angles-right"></i></h1>
                                        <h1>{{ mission.itemRequired }}</h1>
                                    </div>
                                    <div class="item-detail-progress">
                                        <div :style="{ width: GetProcent(mission.itemDelivered, mission.itemRequired) + '%' }" class="item-detail-progress-bar"></div>
                                    </div>
                                    <div class="item-detail-subtext">
                                        <button @click="DeliverItems(index)">Aflevere Items</button>
                                        <h1>+{{mission.reward}} Rep</h1>
                                    </div>
                                </div>
                            </div>
                            <div class="item-completed" v-if="IsObjectiveCompleted(mission.itemRequired, mission.itemDelivered)">
                                <button @click="CompleteTask(index)" title="Fuldfør Mission"><i class="fa-solid fa-check"></i></button>
                            </div>
                        </div>
                        <div class="column-item-image">
                            <img :src="'bck/' + mission.img" alt="">
                        </div>
                    </div>
                </div>
                <div class="profile-column right">
                    <div class="profile-right">
                        <div class="profile-level">
                            <h1>{{ nextReputation.level - 1 }}</h1>
                            <h1><i class="fa-solid fa-angles-right"></i></h1>
                            <h1>{{ nextReputation.level }}</h1>
                        </div>
                        <div class="profile-unlocks">
                            <h1>Næste Upgrade Unlocker:</h1>
                            <ul>
                                <li v-for="(item, index) in nextReputation.unlocks" :key="index">- {{ item }}</li>
                            </ul>
                        </div>
                    </div>
                    <div class="profile-button">
                        <button @click="levelup()" :disabled="!CanLevelUp()">{{ CanLevelUp() ? "Opgradere" : "Ikke tilgændelig"}}</button>
                    </div>
                </div>
            </div>
            <div class="profile-buttons">
                <div class="profile-button">
                    <button v-if="selectedPage === 'rep'" @click="refresh()">Refresh Missioner</button>
                    <button @click="close()">Forlad Shoppen</button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import Swal from 'sweetalert2';

export default {
    props: {
        CanLevelUp: {},
        nextReputation: {},
        GetProcent: {},
        reputationMissions: {},
        GetFormatedPrice: {},
        orderedItems: {},
        NameResource: {},
        close: {},
    },

    data: function(){
        return({
            selectedPage: 'orders',
        })
    },

    methods: {
        SelectPage: function(name) {
            this.selectedPage = name;
        },

        GetDeliverTime: function(totalMinutes) {
            let seconds = totalMinutes * 60;

            var days = Math.floor(seconds / (3600*24));
            var hours = Math.floor(seconds % (3600*24) / 3600);
            var minutes = Math.floor(seconds % 3600 / 60);

            var dDisplay = days >= 0 ? days + (days == 1 ? " dag - " : " dage - ") : "";
            var hDisplay = hours >= 0 ? hours + (hours == 1 ? " time - " : " timer - ") : "";
            var mDisplay = minutes >= 0 ? minutes + (minutes == 1 ? " minut" : " minutter") : "";
            return dDisplay + hDisplay + mDisplay;
        },

        levelup: function() {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/levelup`).catch(function (error) {
                console.log(error + ' : Error at levelup');
            });
        },

        refresh: function() {
            const NameResource = this.NameResource[0];

            Swal.fire({
                title: 'Er du sikker på du vil Refresh alle missioner? Alt data bliver slettet.',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Nej`,
            }).then((result) => {
                if (result.isConfirmed) {
                    axios.post(`https://${NameResource}/refresh`).catch(function (error) {
                        console.log(error + ' : Error at refresh');
                    });
                }
            })
        },

        DeliverItems: function(index) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/deliver`, {
                index: index,
            }).catch(function (error) {
                console.log(error + ' : Error at deliver');
            });

            this.close();
        },

        CompleteTask: function(index) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/complete`, {
                index: index,
            }).catch(function (error) {
                console.log(error + ' : Error at complete');
            });
        },

        cancelOrder: function(index) {
            const NameResource = this.NameResource[0];

            Swal.fire({
                title: 'Er du sikker på at du annullere din ordre? Du får kun 75% tilbage af hvad du betalte.',
                showDenyButton: false,
                showCancelButton: true,
                confirmButtonText: `Ja`,
                cancelButtonText: `Nej`,
            }).then((result) => {
                if (result.isConfirmed) {
                    axios.post(`https://${NameResource}/cancelorder`, {
                        index: index,
                    }).catch(function (error) {
                        console.log(error + ' : Error at cancelorder');
                    });
                }
            })
        },

        collectOrder: function(index) {
            const NameResource = this.NameResource[0];
            axios.post(`https://${NameResource}/collectorder`, {
                index: index,
            }).catch(function (error) {
                console.log(error + ' : Error at collectorder');
            });

            this.close();
        },

        IsObjectiveCompleted: function(required, delivered) {
            return required <= delivered;
        },

        IsObjectEmpty: function(obj) {
            return Object.keys(obj).length <= 0;
        },
    },

    computed: {
        organizedOrders() {
            let orders = [];

            for (let key in this.orderedItems) {
                let cart = this.orderedItems[key];

                cart.weapons = "";
                cart.totalPrice = 0;

                let arrayCount = 0;
                for (let i = 0; i < cart.items.length; i++) {
                    arrayCount++;

                    const cartItem = cart.items[i];
                    let itemListLabel = cartItem.item.label + " x" + cartItem.quantity;
                    if (arrayCount != cart.items.length) {
                        itemListLabel = itemListLabel + ", ";
                    }

                    cart.totalPrice += cartItem.item.price * cartItem.quantity;
                    cart.weapons = cart.weapons + itemListLabel;
                }

                orders.push(cart);
            }

            return orders;
        }
    }
}
</script>

<style>
.profile {
    display: flex;
    flex-flow: column;
    width: 100%;
    height: calc(100% - 100px);
}

.profile-header {
    display: flex;
    align-items: center;
    justify-content: space-around;
    width: 100%;
    height: 60px;
    background-color: rgba(55,55,55,0.75);
}

.profile-header .profile-header-item {
    font-size: 13px;
    text-transform: uppercase;
    color: #606060;
    opacity: 0.75;
}

.profile-header .profile-header-item.selected h1 {
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.profile-header .profile-header-item h1:hover {
    cursor: pointer;
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.profile-content {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    width: 100%;
    height: calc(100% - 60px);
    padding: 20px;
}

.profile-orders {
    padding: 10px 20px;
    background-color: #1b2429;
    height: calc(100% - 70px);
}

.profile-orders.nodata {
    display: flex;
    justify-content: center;
    align-items: center;
}

.profile-orders .profile-empty-items h1 {
    font-size: 2.5em;
    text-transform: uppercase;
    color: #f6f6f6;
}

.profile-content .profile-items {
    border-collapse: collapse;
    width: 100%;
}

.profile-content .profile-items td, .profile-items th {
    padding: 8px;
    height: 45px;
    text-align: center;
}

.profile-items th {
    text-transform: uppercase;
}

.profile-items tbody {
    display: block;
    max-height: 440px;
    overflow: auto;
}

.profile-items thead, tbody tr {
    display: table;
    width: 100%;
    table-layout: fixed;
}

.profile-content .profile-items th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: center;
    color: white;
    font-size: 18px;
}

.profile-content .profile-items th, .profile-content .profile-items td {
    border-right: 1px solid #f6f6f6;
    border-bottom: 1px solid #f6f6f6;
    color: #f6f6f6;
}

.profile-content .profile-items th:last-child, .profile-content .profile-items td:last-child {
    border-right: none;
}

.profile-content .profile-items tr:last-child td {
    border-bottom: none;
}

.profile-items tbody::-webkit-scrollbar {
    display: none;
}

.profile-items td p {
    max-height: 40px;
    overflow-y: auto;
    padding-right: 0.5em;
}

.profile-items td p::-webkit-scrollbar {
    width: 0.5em;
}

.profile-items td p::-webkit-scrollbar-track {
    box-shadow: inset 0 0 6px rgba(13, 17, 52, 0.3);
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
}

.profile-items td p::-webkit-scrollbar-thumb {
    background-color: #f6f6f6;
    border-radius: 2px;
}

.profile-items td.status {
    color: #ef5a4c;
    text-transform: uppercase;
    animation: pulsate 3s ease-out;
    animation-iteration-count: infinite;
    -webkit-animation: pulsate 3s ease-out;
    -webkit-animation-iteration-count: infinite;
    opacity: 0.5;
}

.profile-items td.status.delivered {
    color: #48b120;
}

.profile-items td button {
    text-transform: uppercase;
    font-size: 16px;
    border-radius: 3px;
    color: #f6f6f6;
    background: none;
    border: none;
}

.profile-items td button:hover {
    font-weight: bold;
}

.profile-items input:focus {
    outline: none;
}

.profile-buttons {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;
    padding: 10px 20px 0px 20px;
    border-top: 1px solid #f6f6f6;
    margin-top: 10px;
}

.profile-buttons .profile-button button {
    text-transform: uppercase;
    font-weight: bold;
    color: #f6f6f6;
    background: none;
    border: none;
    width: 200px;
    height: 50px;
    border: 1px solid #f6f6f6;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-right: 20px;
}

.profile-buttons .profile-button h1 {
    text-transform: uppercase;
    font-weight: bold;
    color: #f6f6f6;
    font-size: 16px;
}

.profile-buttons .profile-button button:hover {
    background-color: #1b2429;
}

.profile-rep {
    display: flex;
    height: calc(100% - 70px);
}

.profile-column {
    display: flex;
    width: 100%;
    height: 100%;
    margin: 0px 10px;
}

.profile-column.left {
    flex: 1.5;
    flex-wrap: wrap;
    align-content: flex-start;
    max-height: 100%;
    overflow: auto;
}

.profile-column.left::-webkit-scrollbar {
    display: none;
}

.profile-column.right {
    flex: 1;
    background-color: #1b2429;
    flex-flow: column;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.profile-column-item {
    width: 100%;
    height: 150px;
    margin: 5px;
    background-color: #303030;
    box-shadow: rgba(0,0,0,0.24) 0px 3px 8px;
    position: relative;
    display: flex;
}

.profile-column-item .column-item-content {
    z-index: 100;
    width: 100%;
    display: flex;
    align-items: center;
    padding: 10px;
    background-color: rgba(0,0,0,0.40);
}

.profile-column-item .item-column {
    width: 100%;
    display: flex;
}

.profile-column-item .item-column-subject {
    flex: 1;
    display: flex;
    flex-flow: column;
    justify-content: center;
    padding: 0px 20px;
    color: #f6f6f6;
    -webkit-text-fill-color: #f6f6f6;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
}

.profile-column-item .item-column-subject.right {
    flex: 2;
}

.profile-column-item .item-column-subject h4 {
    font-size: 2em;
    text-transform: uppercase;
    text-align: center;
}

.item-column .item-detail-progress {
    width: 100%;
    background-color: #484d50;
}

.item-column .item-detail-progress-bar {
    height: 5px;
    background-color: #f6f6f6;
}

.profile-column-item .item-detail-text {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    color: #f6f6f6;
    padding: 0px 10px 5px 10px;
}

.profile-column-item .item-detail-text h1 {
    display: flex;
    align-items: baseline;
}

.profile-column-item .item-detail-text i {
    margin: 0px 5px;
    font-size: .8em;
}

.profile-column-item .item-detail-subtext {
    display: flex;
    flex-flow: column;
    justify-content: center;
}

.profile-column-item .item-detail-subtext button {
    text-transform: uppercase;
    font-weight: bold;
    background: none;
    border: none;
    margin-top: 10px;
    font-size: 1.5em;
    color: #f6f6f6;
    -webkit-text-fill-color: #f6f6f6;
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: black;
}

.profile-column-item .item-detail-subtext button:hover {
    cursor: pointer;
    color: #f6f6f6;
    text-shadow: 0 0 2.5px #f6f6f6;
    transition: all 0.5s ease;
}

.profile-column-item .item-detail-subtext h1 {
    text-align: center;
    font-size: 1.25em;
    text-transform: uppercase;
    color: #f6f6f6;
}

.profile-column-item:first-child {
    margin-top: 0;
}

.profile-column-item:last-child {
    margin-bottom: 0;
}

.profile-column-item .item-completed {
    position: absolute;
    bottom: 0;
    right: 0;
    margin: 5px;
}

.profile-column-item .item-completed button {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #f6f6f6;
    background: none;
    margin: 0;
    width: 30px;
    height: 30px;
    border: 1px solid #f6f6f6;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    animation: pulsate 3s ease-out;
    animation-iteration-count: infinite;
    -webkit-animation: pulsate 3s ease-out;
    -webkit-animation-iteration-count: infinite;
    opacity: 0.5;
}

.profile-column-item .column-item-image {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
}

.profile-column-item .column-item-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.profile-column .profile-level {
    display: flex;
    justify-content: space-evenly;
    align-items: baseline;
    color: #f6f6f6;
    padding: 20px 10px;
    width: 100%;
    border-bottom: 1px solid #f6f6f6;
}

.profile-column .profile-level h1 {
    display: flex;
    align-items: baseline;
}

.profile-column .profile-level i {
    margin: 0px 5px;
    font-size: .8em;
}

.profile-column .profile-unlocks {
    display: flex;
    flex-flow: column;
    margin: 20px;
}

.profile-column .profile-unlocks h1 {
    font-size: 1.5em;
    text-transform: uppercase;
    color: #f6f6f6;
}

.profile-column .profile-unlocks ul {
    color: #f6f6f6;
    margin: 20px;
    font-size: 1.5em;
    max-height: 200px;
    overflow: auto;
}

.profile-column .profile-unlocks ul::-webkit-scrollbar {
    display: none;
}

.profile-right {
    width: 90%;
}

.profile-column.right .profile-button button {
    text-transform: uppercase;
    font-weight: bold;
    color: #f6f6f6;
    background: none;
    border: none;
    width: 200px;
    height: 50px;
    border: 1px solid #f6f6f6;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    margin-bottom: 20px;
}

.profile-column.right .profile-button button:disabled,
.profile-column.right .profile-button button[disabled] {
  background-color: rgba(0,0,0,0.40);
  cursor: not-allowed;
}

@keyframes pulsate {
    0% { 
        opacity: 0.5;
    }
    50% { 
        opacity: 1.0;
    }
    100% { 
        opacity: 0.5;
    }
}
</style>