<template>
    <div class="thermite-minigame">
        <div class="minigame">
            <div class="splash" v-if="isHidden">
                <div class="fa hacker">&#xf21b;</div>
                Remote Sequencing Required
            </div>

            <div v-if="!isHidden" class="groups">
                <div class="group" v-for="(box, index) in boxes" :key="index" @click="SelectBox(index)" :class="[box.isGood ? 'good' : '', box.isProper ? 'proper' : '', box.isBad ? 'bad' : '']" :style="{ width: GetSizeProcent() + '%', height: GetSizeProcent() + '%'}"></div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        miniGameRespond: {},
        NameResource: {},
        close: {},
    },

    data: function() {
        return({
            isStarted: false,
            isHidden: true,
            timer_start: 0,
            timer_finish: 0,
            timer_game: 0,
            timer_end: 0,
            rightSelected: 0,
            wrongSelected: 0,
            selectedSpeed: 0,
            selectedMode: 0,
            positions: 0,
            good_positions: 0,
            mode_data: {},
            boxes: [],
        })
    },

    methods: {
        StartMiniGame: function(mode, speed) {
            if (mode == undefined) mode = 5;
            this.selectedMode = mode;

            if (speed == undefined) speed = 30;
            this.selectedSpeed = speed;

            this.rightSelected = 0;
            this.wrongSelected = 0;

            this.mode_data[5] = 10;
            this.mode_data[6] = 14;
            this.mode_data[7] = 18;
            this.mode_data[8] = 20;
            this.mode_data[9] = 24;
            this.mode_data[10] = 28;

            this.positions = this.range(0, Math.pow(this.selectedMode, 2) - 1);
            this.shuffle(this.positions);

            this.good_positions = this.positions.slice(0, this.mode_data[this.selectedMode]);
            for (let i=0; i < this.positions.length; i++){
                this.boxes.push({ isSelected: false, isProper: false, isGood: false, isBad: false });
            }

            let selector = this;
            selector.timer_start = selector.sleep(2000, function() {
                selector.isHidden = false;
                
                selector.good_positions.forEach( pos => {
                    selector.boxes[pos].isGood = true;
                });

                selector.timer_game = selector.sleep(4000, function() {
                    selector.good_positions.forEach( pos => {
                        selector.boxes[pos].isGood = false;
                    });

                    selector.isStarted = true;

                    selector.timer_finish = selector.sleep((selector.selectedSpeed * 1000), function(){
                        selector.isStarted = false;
                        selector.wrongSelected = 3;

                        selector.CheckSelectedBoxes();
                    });
                });
            });
        },

        SelectBox: function(index) {
            if(!this.isStarted) return;
            if(this.boxes[index] == undefined) return;
            if(this.boxes[index].isSelected == true) return;

            let isCorrectBox = this.good_positions.indexOf(parseInt(index)) !== -1;
            if (isCorrectBox == true) {
                this.rightSelected++;
                this.boxes[index].isGood = true;
            }

            if (isCorrectBox == false) {
                this.wrongSelected++;
                this.boxes[index].isBad = true;
            }

            this.boxes[index].isSelected = true;
            this.CheckSelectedBoxes();
        },

        CheckSelectedBoxes: function() {
            if (this.wrongSelected >= 3) {
                this.EndMiniGame(false);
                return;
            }

            if (this.rightSelected >= this.mode_data[this.selectedMode]) {
                this.EndMiniGame(true);
                return;
            }
        },

        EndMiniGame: function(hasWon) {
            this.isStarted = false;

            if(!hasWon) {
                this.good_positions.forEach( pos => {
                    this.boxes[pos].isProper = true;
                });
            }
            
            let selector = this;
            selector.timer_start = selector.sleep(3000, function() {
                selector.ResetMiniGame(hasWon);
            });
        },

        ResetMiniGame: function(hasWon) {
            clearTimeout(this.timer_start);
            clearTimeout(this.timer_game);
            clearTimeout(this.timer_finish);
            clearTimeout(this.timer_end);

            this.isStarted = false;
            this.isHidden = true;
            
            this.rightSelected = 0;
            this.wrongSelected = 0;

            this.selectedSpeed = 0;
            this.selectedMode = 0;

            this.positions = 0;
            this.good_positions = 0;

            this.mode_data = {};
            this.boxes = [];

            this.miniGameRespond(hasWon);
        },

        range: function(start, end, length = end - start + 1) {
            return Array.from({length}, (_, i) => start + i)
        },

        shuffle: function(array) {
            for (let i = array.length - 1; i > 0; i--) {
                const j = Math.floor(Math.random() * (i + 1));
                const temp = array[i];
                array[i] = array[j];
                array[j] = temp;
            }
        },

        sleep: function(ms, fn) {
            return setTimeout(fn, ms);
        },

        GetSizeProcent: function() {
            return Math.round(100 / this.selectedMode);
        },
    },
}
</script>

<style>
.thermite-minigame {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.thermite-minigame .minigame {
    margin: 0 auto;
    width: 540px;
    height: 540px;
    min-width: 540px;
    max-width: 540px;
    background-color: #232832;
    padding: 10px;
}

.thermite-minigame .splash {
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 100%;
    margin: 220px auto;
    color: white;
    font-size: 16px;
}

.thermite-minigame .splash .hacker {
    font-size: 65px;
    margin-bottom: 30px;
}

.thermite-minigame .groups {
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    user-select: none;
}

.thermite-minigame .group {
    width: 74px;
    height: 74px;
    border: 8px #232832 solid;
    background-color: #34475C;
}

.thermite-minigame .jewelry .group {
    width: 92px;
    height: 92px;
}

.thermite-minigame .proper {
    background-color: #006600;
}

.thermite-minigame .good {
    background-color: #CCCCCC;
}

.thermite-minigame .bad {
    background-color: #F00F18;
}
</style>